import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './pages/home/home.component';
import { ArquitecturaSoftwareComponent } from './pages/blog/arquitectura-software/arquitectura-software.component';
import { DriverArquitectonicosComponent } from './pages/blog/arquitectura-software/post/driver-arquitectonicos/driver-arquitectonicos.component';
import { MicroservicioComponent } from './pages/blog/arquitectura-software/post/microservicio/microservicio.component';
import { WorkExperiencienceComponent } from './pages/work-experiencience/work-experiencience.component';
import { BaseDatosComponent } from './pages/blog/base-datos/base-datos.component';

const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'experiencia-laboral', component: WorkExperiencienceComponent},
  {path: 'arquitecturasoftware', component: ArquitecturaSoftwareComponent},
  {path: 'arquitecturasoftware/driver-arquitectonicos', component: DriverArquitectonicosComponent},
  {path: 'arquitecturasoftware/microservicio', component: MicroservicioComponent},
  {path: 'basedatos', component: BaseDatosComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
