import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-nav-bar',
  templateUrl: './nav-bar.component.html',
  styleUrls: ['./nav-bar.component.css']
})
export class NavBarComponent implements OnInit {

  is_active_menu:string = "";
  is_active_contacto:string = "";

  constructor() { }

  ngOnInit(): void {
  }


  btn_isActive():void{
    if( this.is_active_menu ==  "")
      this.is_active_menu = "is-active";
    else
      this.is_active_menu = "";

  }

  btn_isActive_button():void{
    if( this.is_active_contacto ==  "")
      this.is_active_contacto = "is-active";
    else
      this.is_active_contacto = "";

  }
}
