import { BaseDatosComponent } from './pages/blog/base-datos/base-datos.component';
import { NzAlertModule } from 'ng-zorro-antd/alert';

registerLocaleData(en);

@NgModule({
  declarations: [
    AppComponent,
    NavBarComponent,
    FooterComponent,
    HomeComponent,
    ArquitecturaSoftwareComponent,
    DriverArquitectonicosComponent,
    MicroservicioComponent,
    WorkExperiencienceComponent,
    DomseguroPipe,
    BaseDatosComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    NzCarouselModule,
    NzProgressModule,
    NzCardModule,
    NzGridModule,
    NzImageModule,
    NzIconModule,
    NzAlertModule
  ],
  providers: [{ provide: NZ_I18N, useValue: en_US }],
  bootstrap: [AppComponent]
})
export class AppModule { }
